@font-face {
    font-family: 'Ownglyph_ParkDaHyun';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2411-3@1.0/Ownglyph_ParkDaHyun.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.find-store-container {
    padding: 20px;
    font-family: 'Ownglyph_ParkDaHyun', sans-serif;
}

.restaurant-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.restaurant-card {
    padding: 15px;
    width: calc(25% - 20px);
    box-sizing: border-box;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.restaurant-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.restaurant-card img {
    width: 100%;
    height: 200px;
    object-fit: fill;
    border-radius: 4px;
}

.restaurant-card h2 {
    font-size: 24px;
    margin: 10px 0 5px;
    color: #ff5722;
}

.restaurant-card p {
    margin: 5px 0;
    font-size: 20px;
    color: #484848;
}

.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 20px;
}

.pagination button {
    padding: 10px 15px;
    border: none;
    background-color: #ffe0b2;
    border-radius: 50%;
    cursor: pointer;
    font-size: 16px;
    color: #ff5722;
    transition: background-color 0.2s ease, transform 0.2s ease;
}

.pagination button:disabled {
    background-color: #ffd699;
    cursor: not-allowed;
}

.pagination button:hover:not(:disabled) {
    background-color: #ffa726;
    color: white;
    transform: scale(1.1);
}

.page-indicator {
    font-size: 16px;
    font-weight: bold;
    color: #ff9800;
    padding: 0 10px;
    flex-grow: 1;
    text-align: center;
}